export const setAtt = {
    methods: {
        async setMsg(axios, store, id) {
            //根据传入的userid，设置vuex
            store.dispatch("clear");
            let result1 = await axios.get(`${this.CURL}users/cart/${id}`)
            if (result1.data.msg.length > 0) {
                //用户购物车不为空
                let data1 = result1.data.msg;
                let cart = []
                let userchosen = []
                //存放用户购物车信息
                data1.forEach((item) => {
                    let obj = new Object();
                    obj.cartid = item.cartid;
                    obj.itemname = item.itemname;
                    obj.memory = item.memory;
                    obj.color = item.color;
                    obj.amount = item.amount;
                    obj.cartimage = item.cartimage;
                    obj.price = item.price;
                    obj.maxamounts = item.maxamounts;
                    obj.chosen = item.chosen;
                    cart.push(obj);

                    let obj2 = new Object();
                    if (item.chosen == 1) {
                        obj2.cartid = item.cartid;
                        obj2.chosen = item.chosen;
                        obj2.amount = item.amount;
                        obj2.cartimage = item.cartimage;
                        obj2.memory = item.memory;
                        obj2.price = item.price;
                        obj2.color = item.color;
                        obj2.itemname = item.itemname;
                        userchosen.push(obj2);
                    }
                })
                //提交用户购物车和购物车选择情况
                store.dispatch("setusercart", cart)
                store.dispatch("setuserchosen", userchosen)
            } else {
                //为空就要查user表
                // let result2 = await axios.get(`http://localhost:9999/users/${id}`)
                // let result2 = await axios.get(`${this.CURL}users/${id}`)
                // console.log(result2, "result2");
                // store.dispatch("setusercart", [])
                // store.dispatch("setuserchosen", [])
            }

            let url2 = `${this.CURL}users/order/all?id=${id}`
            let result3 = await axios.get(url2)
            result3 = result3.data.msg;
            // console.log(result3, "result3");
            let contain = [],obj = {},products,productObj,total;
            result3.forEach(items => {
                if (items.orderid !== obj.orderid) {
                    obj = {},products = [],productObj = {},total = 0;
                    obj.address = items.address;
                    obj.orderid = items.orderid;
                    obj.addressid = items.addressid;
                    obj.phone = items.phone.slice(0, 3) + "****" + items.phone.slice(7);
                    for (let item in items) {
                        if(item == "id") {
                            productObj.id = items["id"];
                        } else if (item == "amount") {
                            productObj.amount = items["amount"];
                        } else if (item == "color") {
                            productObj.color = items["color"];
                        } else if (item == "cartimage") {
                            productObj.cartimage = items["cartimage"];
                        } else if (item == "memory") {
                            productObj.memory = items["memory"];
                        } else if (item == "itemname") {
                            productObj.itemname = items["itemname"];
                        } else if (item == "price") {
                            productObj.price = items["price"];
                        }
                    }
                    products.push(productObj)
                    obj.products = products;
                    obj.receivername = items.receivername;
                    obj.region = items.region;
                    obj.remarks = items.remarks;
                    obj.time = this.transformTime(items.time);
                    obj.username = items.username;
                    total = items.amount * items.price;
                    obj.total = total;
                    contain.push(obj)
                } else if(items.orderid === obj.orderid) {
                    let productObj1 = {};
                    for (let item in items) {
                        if(item == "id") {
                            productObj1.id = items.id;
                        } else if (item == "amount") {
                            productObj1.amount = items.amount;
                        } else if (item == "color") {
                            productObj1.color = items.color;
                        } else if (item == "cartimage") {
                            productObj1.cartimage = items.cartimage;
                        } else if (item == "memory") {
                            productObj1.memory = items.memory;
                        } else if (item == "itemname") {
                            productObj1.itemname = items.itemname;
                        } else if (item == "price") {
                            productObj1.price = items.price;
                        }
                    }
                    total += items.amount * items.price;
                    obj.total = total;
                    products.push(productObj1)
                    obj.products = products;
                }
            })
            // console.log(contain,"contain");
            store.dispatch("setuserorder", contain);


            let url3 = `${this.CURL}users/receivergoods/all?id=${id}`
            let result4 = await axios.get(url3)
            result4 = result4.data.msg;
            // console.log(result4,"result4");
            let contain1 = [],reObj = {},reProducts = [],reproductObj = {},retotal;
            result4.forEach(items => {
                if (items.receiverid !== reObj.receiverid) {
                    reObj = {},reProducts = [],reproductObj = {},retotal = 0;
                    reObj.address = items.address;
                    reObj.receiverid = items.receiverid;
                    reObj.addressid = items.addressid;
                    reObj.phone = items.phone.slice(0, 3) + "****" + items.phone.slice(7);
                    for (let item in items) {
                        if(item == "id") {
                            reproductObj.id = items["id"];
                        } else if (item == "amount") {
                            reproductObj.amount = items["amount"];
                        } else if (item == "color") {
                            reproductObj.color = items["color"];
                        } else if (item == "cartimage") {
                            reproductObj.cartimage = items["cartimage"];
                        } else if (item == "memory") {
                            reproductObj.memory = items["memory"];
                        } else if (item == "itemname") {
                            reproductObj.itemname = items["itemname"];
                        } else if (item == "price") {
                            reproductObj.price = items["price"];
                        }
                    }
                    reProducts.push(reproductObj)
                    reObj.reProducts = reProducts;
                    reObj.username = items.username;
                    reObj.region = items.region;
                    reObj.time = this.transformTime(items.time);
                    reObj.username = items.username;
                    retotal = items.amount * items.price;
                    reObj.retotal = retotal;
                    contain1.push(reObj)
                } else if(items.receiverid === reObj.receiverid) {
                    let reProductObj1 = {};
                    for (let item in items) {
                        if(item == "id") {
                            reProductObj1.id = items.id;
                        } else if (item == "amount") {
                            reProductObj1.amount = items.amount;
                        } else if (item == "color") {
                            reProductObj1.color = items.color;
                        } else if (item == "cartimage") {
                            reProductObj1.cartimage = items.cartimage;
                        } else if (item == "memory") {
                            reProductObj1.memory = items.memory;
                        } else if (item == "itemname") {
                            reProductObj1.itemname = items.itemname;
                        } else if (item == "price") {
                            reProductObj1.price = items.price;
                        }
                    }
                    retotal += items.amount * items.price;
                    reObj.retotal = retotal;
                    reProducts.push(reProductObj1)
                    reObj.reProducts = reProducts;
                }
            })
            // console.log(contain1,"contain1");
            store.dispatch("setuserreceivergoods", contain1);
        },
        async setUserChosen(axios, store, id) {
            //作用：购物车选择修改后设置Vuex
            let result1 = await axios.get(`${this.CURL}users/cart/${id}`)
            if (result1.data.msg.length > 0) {
                //用户购物车不为空
                let userchosen = []
                let data1 = result1.data.msg;
                data1.forEach((item) => {
                    let obj2 = new Object();
                    if (item.chosen == 1) {
                        obj2.cartid = item.cartid;
                        obj2.chosen = item.chosen;
                        obj2.amount = item.amount;
                        obj2.cartimage = item.cartimage;
                        obj2.memory = item.memory;
                        obj2.price = item.price;
                        obj2.color = item.color;
                        obj2.itemname = item.itemname;
                        userchosen.push(obj2);
                    }
                })
                store.dispatch("setuserchosen", userchosen)
            }
        },
        async setUserCart(axios, store, id) {
            //作用：购物车数量更改后设置Vuex
            let result1 = await axios.get(`${this.CURL}users/cart/${id}`)
            if (result1.data.msg.length > 0) {
                //用户购物车不为空
                let data1 = result1.data.msg;
                let cart = []
                data1.forEach((item) => {
                    let obj = new Object();
                    obj.cartid = item.cartid;
                    obj.itemname = item.itemname;
                    obj.memory = item.memory;
                    obj.color = item.color;
                    obj.amount = item.amount;
                    obj.cartimage = item.cartimage;
                    obj.price = item.price;
                    obj.maxamounts = item.maxamounts;
                    obj.chosen = item.chosen;
                    cart.push(obj);
                })
                store.dispatch("setusercart", cart)
            }
        },
        getOrderId(array) {
            //从数组中获取不同的OrderId
            let array2 = new Array();
            array.forEach((item) => {
                if (array2.indexOf(item.orderid) == -1) {
                    array2.push(item.orderid)
                }
            })
            return array2
        },
        getReceiveId(array) {
            //从数组中获取不同的OrderId
            let array2 = new Array();
            array.forEach((item) => {
                if (array2.indexOf(item.receiveid) == -1) {
                    array2.push(item.receiveid)
                }
            })
            return array2
        },

        transformTime(str) {
            //将日期转换成需要的格式
            let time = str.split("T")
            let dateArray = time[0].split("-")
            time[1] = time[1].substring(0, 8)
            let timeArray = time[1].split(":")
            let year = dateArray[0]
            let month = dateArray[1]
            let day = dateArray[2]

            let hour = parseInt(timeArray[0])
            hour = (hour + 8) > 24 ? (hour - 16) : (hour + 8)
            if (hour < 10) {
                hour = "0" + hour
            }
            let minute = timeArray[1]

            let date = `${year}年${month}月${day}日 ${hour}:${minute}`
            return date
        },
    }
} 