<template>
  <div id="app">
    <router-view  v-if="isRouterAlive"></router-view>
  </div>
</template>

<script>
import {setAtt} from "./mixin/setAtt.js"
export default {
  name: "App",
  components: {},
  mixins:[setAtt],
  data() {
    return {
      isRouterAlive: true,
    };
  },
  provide() {
    //提供
    return {
      reload: this.reload,
    };
  },
  methods:{
    reload() {
        this.isRouterAlive = false;
          this.$nextTick(function () {
            this.isRouterAlive = true;
          });
    },
  },
  mounted() {
    if (this.$cookie.get("userId")) {
      //如果确实有userId
      //则设置Vuex
       this.setMsg(this.axios, this.$store, this.$cookie.get("userId"));
       this.$store.commit("SETUSERID", this.$cookie.get("userId"))
       this.$store.commit("SETUSERNAME", this.$cookie.get("userName"))
    }
  },
  beforeDestroy() {
    // this.$store.dispatch("clear");
    // this.$cookie.delete("userId");
  },
};
</script>

